@keyframes blink {
  50% {
    background: #fff; } }

th, td {
  white-space: normal !important;
  text-overflow: initial !important; }

.bordered--table {
  border: 1px solid #e0e0e0; }

.white-space-wrap {
  white-space: normal !important; }

.table-loading-shell {
  background: #f6f6f6;
  animation-name: blink;
  animation-timing-function: linear;
  animation-duration: 0.5s;
  animation-iteration-count: infinite; }
