/**
 * # Notifications
 */
.notification {
  /* box-sizing: border-box; */
  position: fixed;
  top: 70px;
  right: -353px;
  padding: 20px;
  min-width: 300px;
  max-width: 420px;
  /* z-index: 0; */
  border-radius: 2px;
  /* text-align: left; */
  z-index: 2;
  color: #fff;
  font-weight: bold;
  box-shadow: 0 2px 4px 0 #9b9b9b;
  text-align: left;
  color: #fff;
  font-weight: bold; }
  .notification > span.fa {
    position: absolute;
    left: 20px;
    height: 24px;
    width: 24px;
    line-height: 24px;
    border-radius: 100%;
    text-align: center; }

.notification {
  border: 1px solid; }
  .notification.default {
    background: #cce5ff;
    border-color: #b8daff;
    color: #004085; }
    .notification.default span svg {
      fill: #fff; }
  .notification.success {
    border-color: #c3e6cb;
    background: #d4edda;
    color: #155724; }
    .notification.success span svg {
      fill: #155724; }
  .notification.warning {
    border-color: #ffeeba;
    background: #fff3cd;
    color: #856404; }
    .notification.warning span svg {
      fill: #856404; }
  .notification span {
    vertical-align: top;
    display: inline-block; }

.notification .btn-undo {
  display: block;
  margin-top: 10px;
  height: 28px;
  width: 60px; }
